<template>
  <section class="hero is-success is-fullheight"></section>
</template>

<script>
export default {
  name: 'welcome',
  data() {
    return {
      msg: 'COMING SOON!',
    };
  },
};
</script>

<style scoped>
.logo {
  max-height: 6em;
}
.hero .nav {
  height: 8rem;
}

.button.is-primary {
  background-color: #252828;
  border-color: transparent;
  color: #fff;
  border-radius: 100px;
  padding: 25px 30px;
}

.hero-footer.has-text-right {
  padding-bottom: 25px;
}

.title, .subtitle {
  color: #3F51B5;
}

.title:after {
  content: '';
  position: relative;
  display: block;
  width: 200px;
  height: 5px;
  background: #fff;
  margin: 15px auto;
  transition: width 250ms ease-in-out;
  border-radius: 100px;
}

.hero-body:hover .title:after {
  width: 400px;
}
.hero.is-success {
    background: url('../assets/cover.png');
    color: #fff;
    background-position: center center;
    background-size: cover;
}
</style>

<style>
  @media (max-width: 720px) {
    .title {
      font-size: 1.5rem;
    }
  }
</style>



